export default {
	data() {
		return {
			textareaVal:null,
		}
	},
	onLoad() {

	},
	methods: {
		async postMsg(){
			if( this.textareaVal != null ){
				let content = encodeURI(this.textareaVal)
				try {
					let data = await this.$wxhttp.post({
						url: '/user/v1/sendFeedback',
						data:{
							content:content
						}
					})
						
					if( data.data.success == 1 ){
						uni.showToast({
							title: '提交成功',
							icon:"success",
							duration:1000
						})
						setTimeout(res=>{
							uni.navigateBack()
						},1100)
					}
				} catch (e) {
					
				}
			}else{
				uni.showToast({
					title: '请填写反馈内容',
					icon:"none",
					duration:1000
				})
			}
			
		}
	}
}
